@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  overflow: hidden; /* Prevent scrollbars from appearing */
}

.moving-background {
  position: relative;
  height: 100vh; /* Full viewport height */
  background-image: url("./assets/background.jpg");
  background-size: cover; /* Ensures the image covers the entire background */
  background-repeat: no-repeat;
  animation: move-background 60s linear infinite; /* Adjust the duration as needed */
}

@keyframes move-background {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 100% 100%; /* Move diagonally across the background */
  }
}

body {
  font-family: "Outfit", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.6rem;
}

h1 {
  font-size: 6rem;
}

.app {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.4);
  color: #fff;
}

.app:before {
  content: "";
  background: url("./assets/background.jpg") no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.app .search {
  text-align: center;
  padding: 1rem;
}

.app input {
  padding: 0.7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  background: rgba(255, 255, 255, 0.1);
  color: #f8f8f8;
}

::placeholder {
  color: #f8f8f8;
}

.city-name {
  text-align: center;
}

.container {
  max-width: 800px;
  height: 700px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top {
  width: 100%;
  margin: 1rem auto;
}

.app .description {
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg);
}

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.2);
}

.bold {
  font-weight: 500;
}

.hourly-container {
  display: flex;
  overflow-x: auto; /* Allow horizontal scrolling */
  margin-top: 20px;
  padding: 10px;
  width: 100%;
  scroll-behavior: smooth;
  justify-content: flex-start; /* Align boxes to the left */
}

.hourly-box {
  min-width: 100px; /* Ensure each box has a minimum width */
  max-width: 120px; /* Set a maximum width to avoid oversized boxes */
  background-color: rgba(68, 68, 68, 0.5);
  color: white;
  border-radius: 10px;
  text-align: center;
  padding: 10px;
  margin-right: 10px; /* Space between boxes */
  flex-shrink: 0; /* Prevent shrinking */
}

.hourly-p {
  font-size: 1.2rem;
  font-weight: lighter;
}

.bottom-p {
  font-size: 1.3rem;
  font-weight: 300;
}

.temp {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.temp h1 {
  font-size: 3rem;
  margin: 0;
}

.temp p {
  font-size: 1.5rem;
  margin: 0;
}

/* Responsive styles */
@media (max-width: 600px) {
  .hourly-box {
    min-width: 80px; /* Smaller boxes on smaller screens */
    font-size: 1rem; /* Adjust font size if necessary */
  }

  .app {
    height: auto; /* Allow height to adjust based on content */
  }
}
